<template>
  <div id="box">
    <section>
      <div id="p">
        <p id="top_p">遇见你，遇见世界的美好</p>
        <p id="bottom_p">一个简单的方式，开启你的志愿旅途。</p>
      </div>
      <div id="butt">
        <button @click="register()" id="b_r">注册</button>
        <button @click="login()" id="b_l">登录</button>
      </div>
      <div id="sign"></div>
    </section>
  </div>
</template>

<script>

export default {
  name: 'init',
  data () {
    return {
    }
  },
  mounted () {
    console.log(this)
  },
  methods: {
    register: function () {
      this.$router.push('signup')
    },
    login: function () {
      this.$router.push('signin')
    }
  }
}
</script>

<style scoped>
button {
  outline: none;
}
#box {
  background-image: url("../../assets/init/页面.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  width: 100vw;
}
#sign {
  z-index: -1;
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-top-left-radius: 35px;
  border-top-right-radius: 35px;
}

#sign:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("../../assets/init/页面blur1.png");
  background-size: cover;
  background-attachment: fixed;
  filter: blur(7px);
  margin: -30px;
}

section {
  position: absolute;
  bottom: 0px;
  width: 100%;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 30%;
  padding: 20px 0px;
  animation: toggle 1s ease-out;
}

@keyframes toggle {
  from {
    bottom: -100%;
  }
  to {
    bottom: 0px;
  }
}

#p {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#top_p {
  font-size: 1.6rem;
  color: white;
}

#bottom_p {
  margin-top: 10px;
  font-size: 0.9rem;
  color: white;
}

button {
  background-color: transparent;
  border: 1px solid white;
  border-radius: 50px;
  color: white;
  font-size: 1.4rem;
  height: 3.5rem;
  width: 37%;
  transition: 0.5s;
}

button:hover {
  background-color: white;
  color: black;
}

#b_l {
  background-color: white;
  color: black;
}

#butt {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  justify-content: center;
}

#butt > button:first-child {
  margin-right: 2rem;
}
</style>



