<template>
  <div>
    <transition name="slide-fade" mode="out-in">
      <div v-if="username_page" class="username_page" key="username_page">
        <div class="title">
          <p>登录</p>
        </div>
        <div class="layui-form">
          <input
            class="yujian-input"
            name="username"
            type="text"
            id="phone"
            placeholder="手机号码"
            v-model="username"
          />
          <div class="deal">
            <div>
              <span style="float: right"
                >没有帐户？
                <a href="/#/signup" class="yujian-jump">去注册！</a>
              </span>
            </div>
          </div>
          <input
            type="button"
            class="yujian-input"
            value="下一步"
            v-on:click="switch_page()"
          />
        </div>
      </div>
      <div v-else class="password_page" key="password_page">
        <div class="username">
          <button class="back_logo_button" v-on:click="switch_page()">
            <img class="back_logo" src="../../assets/total/灰箭头.jpg" />
          </button>
          <p>{{ username }}</p>
        </div>
        <div class="title">
          <p>输入密码</p>
        </div>
        <p
          style="
            color: red;
            font-weight: 100;
            font-size: 14px;
            text-align: left;
          "
        >
          {{ msg }}
        </p>
        <div class="layui-form">
          <input
            class="yujian-input"
            name="password"
            type="password"
            id="passwd"
            placeholder="密码"
            v-model="password"
          />
          <div class="deal">
            <div>
              <a href="" class="yujian-jump">忘记了密码？</a>
            </div>
          </div>
          <input
            type="button"
            class="yujian-input"
            value="登录"
            v-on:click="submit()"
          />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { login, accounts } from '@/api/account'
import { Toast } from 'mint-ui';

export default {
  name: 'sign_in',
  data () {
    return {
      username: '',
      password: '',
      username_page: true,
      msg: ''
    }
  },
  methods: {
    switch_page: function () {
      this.username_page = !(this.username_page)
    },
    submit () {
      login(
        {
          'username': this.username,
          'password': this.password
        }
      ).then(res => {
        console.log(res)
        window.localStorage.clear()
        window.localStorage.setItem('token', res.token)
        // 获取用户数据
        accounts().then(res => {
          this.name = res.role_object.name
          this.organization = res.organization
          localStorage.setItem('info', JSON.stringify(res.role_object))
          // localStorage.setItem('')
          // window.location.reload()
          Toast("登录成功，即将跳转")
          this.$router.push('/')
        })
      })
        .catch(error => {
          console.log(error)
          this.msg = '用户名或密码错误'
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.back_logo_button {
  margin-right: 0.5rem;
  background-color: white;
  border: none;
}

.back_logo_button:focus {
  outline: none;
}

.back_logo {
  transform: rotateY(180deg);
  height: 14px;
}

.title {
  margin-bottom: 12px;
  margin-top: 16px;
  font-weight: bold;
  font-size: 28px;
  text-align: left;
}

.username {
  height: 24px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
  font-weight: 400;
  font-size: 0.9375rem;
  margin-top: 16px;
  color: #1b1b1b;
}

.username p {
  display: inline;
}

.deal {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 1rem 0;
  font-size: 0.8rem;
}

.deal > label {
  margin-right: 1rem;
}

.layui-form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-bottom: 40%;
}

.yujian-input[type="text"],
.yujian-input[type="password"] {
  box-sizing: border-box;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom-color: rgba(0, 0, 0, 0.8);
  border-bottom-width: 1px;
  border-radius: 0px;
  margin: 0.8rem 0;
  height: 36px;
  font-size: 1.02rem;
}

.yujian-input[type="text"]:focus,
.yujian-input[type="password"]:focus,
.yujian-input[type="button"]:focus {
  outline: none;
}

#roles {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
}

#roles label {
  line-height: 1rem;
  height: 1rem;
}

#roles label input {
  line-height: 1rem;
  height: 1rem;
  margin-right: 5px;
}

.yujian-input[type="button"] {
  border: none;
  min-width: 108px;
  width: fit-content;
  padding: 6px 12px;
  background-color: #0067b8;
  color: white;
  font-size: inherit;
  line-height: 1.2rem;
  margin-left: auto;
  margin-top: 0.8rem;
}

.yujian-jump {
  color: #0a4c54;
  transition: 0.5s;
  cursor: pointer;
}

.yujian-jump:hover {
  opacity: 0.3;
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-to {
  transform: translateX(-10px);
  opacity: 0;
}
.slide-fade-enter {
  transform: translateX(10px);
  opacity: 0;
}
</style>



