import axios from 'axios'

// 封装 axios 方法，返回值是 Promise
// options 参数为一些 url（请求路径），method（请求方法，默认为get）
// 以及 data
export default function request(options) {
    return new Promise((resolve, reject) => {
        // 创建 axios 实例
        const instance = axios.create({
            // baseUrl：请求后端的前部分的url
            baseURL: 'http://121.43.153.200/',
            // 设置延迟时间（单位：毫秒）
            timeout: 10000
                // headers: {"Content-Type": "application/"}
        })

        // 请求拦截
        instance.interceptors.request.use(config => {
            // console.log(config)
            // 自定义header，如果localstorage中有token,向headers中加入token
            if (localStorage.getItem('token')) config.headers.Authorization = 'token ' + localStorage.getItem('token')
            return config
        })

        // 响应拦截器
        instance.interceptors.response.use(res => {
            // 如果返回值是200
            // console.log(res)
            if (String(res.status).indexOf('20') === 0) {
                return res.data
            } else {
                Toast('请求失败');
            }
        })

        // 通过实例进行网络请求
        instance(options)
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                // console.log(options)
                // alert('请求失败...')
                reject(err)
            })
    })
}


