import request from '@/utils/axios'

export function helpDetail(id) {
    return request({
        url: `/growup/plan/detailed/`,
        method: 'post',
        data: { planID: id }
    })
}

// 所有帮扶 帮扶大事记列表
export function helplist(id) {
    return request({
        url: `/growup/plan/?pairID=${id}`,
    })
}

// 帮扶数据 帮扶数量
export function helpnumber(id) {
    return request({
        url: `/growup/plannumber/`,
        method: 'post',
        data: { pairID: id }
    })
}

// 修改帮扶
export function helpchange(data) {
    return request({
        url: `/growup/plan/`,
        method: 'put',
        data
    })
}

// 建立帮扶
export function helpCreate(data) {
    return request({
        url: `/growup/plan/`,
        method: 'post',
        data
    })
}
// 删除帮扶
export function helpDelete(id) {
    return request({
        url: `/growup/plan/`,
        method: 'delete',
        data: { planID: id }
    })
}


