<template>
  <div id="box">
    <messHead></messHead>
    <messForm :relate_butt="buttValue"></messForm>
  </div>
</template>

<script>
import messHead from '@/components/messHead'
import messForm from '@/components/messForm'

export default {
  name: 'message',
  components: {
    messHead, messForm
  },
  data: function () {
    return {
      buttValue: '结队',
    }
  },
}
</script>


