<template>
  <div id="main">
    <div class="content">
      <div class="orga">
        <div><img src="./assets/logo.png" /></div>
        <div class="p">
          <p>通知ID-{{ info.id }}</p>
          <!-- 设置文字自动隐藏，字符串切割 -->
          <p>您有一个来自孩子：{{ info.recipient_id }} 的结对请求</p>
          <p>{{ info.timestamp }}</p>
        </div>
      </div>
      <div class="img">
        <!-- <img src="./assets/箭头小.png" /> -->
        <p v-if="info.unread">
          <span style="color: #7ec18d" @click="handle(info, true)"
            >同意<br
          /></span>
          <span style="color: #ff5722" @click="handle(info, false)">忽略</span>
        </p>
        <p v-else>已处理</p>
      </div>
    </div>
  </div>
</template>

<script>
import { agreePair, disagreePair } from '@/api/growup'

export default {
  name: 'messList',
  props: ['info'],
  data: function () {
    return {
    }
  },
  mounted () {
  },
  methods: {
    handle (item, isAgree) {
      if (isAgree) {
        // 同意结对请求
        agreePair({ pairID: item.verb }).then(res => {
          // 同意之后将通知删除并刷新页面
           disagreePair({ noticeID: item.id }).then(res => {
          window.location.reload()
        })
        })
      } else {
          // 忽略请求 将通知删除并刷新页面
        disagreePair({ noticeID: item.id }).then(res => {
          window.location.reload()
        })
      }
    }
  }
}
</script>

<style lang="css" scoped>
#main {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.content {
  width: 100%;
  background-color: white;
  margin-bottom: 0.3rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.content > div {
  display: flex;
  flex-direction: row;
}

.orga {
  justify-content: space-between;
  padding: 1rem 1rem 1rem 1rem;
  border-bottom: 0.2px solid #f0f6f7;
}

.orga > div {
  display: flex;
  flex-direction: column;
}

.orga > div:first-child {
  height: 4rem;
  margin-right: 0.7rem;
}

.orga div img {
  height: 100%;
}

.p {
  font-size: 0.8rem;
  display: flex;
  justify-content: column;
  font-weight: lighter;
  align-items: flex-start;
  text-align: left;
}
.p > p:first-child {
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 0.2rem;
}

.p > p:last-child {
  color: #999999;
  margin-top: 0.3rem;
}

.mess {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.4rem 0;
}

.mess > p:last-child {
  font-weight: lighter;
  font-size: 0.9rem;
}

.img {
  height: 1rem;
  margin-top: 2.3rem;
  margin-right: 1rem;
}

.img img {
  height: 100%;
}
</style>


