<template>
  <div id="head_with_back">
    <div @click="$router.back()">
      <img src="../assets/total/箭头黑.png" />
    </div>
    <p v-if="showTitle">{{ title }}</p>
    <!-- <input type="text" v-model="title" style="position: fixed; bottom: 0px" /> -->
    <!-- <input
      type="checkbox"
      v-model="showTitle"
      style="position: fixed; bottom: 0px; right: 0px"
    /> -->
  </div>
</template>

<script>
export default {
  name: 'head_with_back',
  props: {
    title: String,
    showTitle: Boolean
  },
  data () {
    return {

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#head_with_back {
  height: 1.2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0.8rem 5%;
  font-size: 1.2rem;
  background-color: white;
}

#head_with_back > div {
  height: 1.2rem;
  margin-right: 1.5rem;
}

#head_with_back > div > img {
  height: 100%;
}
p {
  font-weight: bold;
}
</style>



