import request from '@/utils/axios'

//获取志愿者列表
export function volunteer() {
    return request({
        url: '/growup/pair/',
        method: 'get',
    })
}

// 返回帮扶数量 data: { pair_id: id }
export function dataget(data) {
    return request({
        url: '/growup/plannumber/',
        method: 'post',
        data
        // transformRequest: [
        //     function(data) {
        //         let ret = ''
        //         for (let it in data) {
        //             ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
        //         }
        //         ret = ret.substring(0, ret.lastIndexOf('&'));
        //         return ret
        //     }
        // ],
        // headers: {
        //     'Content-Type': 'application/x-www-form-urlencoded'
        // }
    })
}

//发送申请结对请求
export function makeTeam(data) {
    return request({
        url: '/growup/pair/',
        method: 'post',
        data: {
            child_id: data.child_id,
            volunteer_id: data.volunteer_id
        }
    })
}

// 志愿者同意结对请求 {pairID: item.verb} 
// notice列表item中的verb字段用作结对请求的Id
export function agreePair(data) {
    return request({
        url: '/growup/pair/',
        method: 'put',
        data
    })
}

//发送申请结对请求
export function getNotice() {
    return request({
        url: '/growup/list/',
    })
}

//获取帮扶计划
export function getPlan(id) {
    return request({
        url: '/growup/plan/?pairID=' + id,
    })
}

//依据noticeID 忽略并删除通知 {noticeID: xxx}
export function disagreePair(data) {
    return request({
        url: '/growup/notice/',
        method: 'delete',
        data
    })
}

// 结束结对 {pairID: xxx}
export function completePair(data) {
    return request({
        url: '/growup/pairend',
        method: 'post',
        data
    })
}


