<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#sign {
  padding: 24px;
}
p {
  position: absolute;
  bottom: 24px;
  left: 24px;
  color: inherit;
  font-size: 0.6rem;
}
</style>



