<template>
  <transition name="slide-fade" mode="out-in">
    <div class="main">
      <personalHeader></personalHeader>
      <personalNav></personalNav>
      <personalData></personalData>
      <personalOthers></personalOthers>
      <footer_
        :logo="logoimg"
        :lcolor="logocolor"
        :plogo="plogoimg"
        :plcolor="plogocolor"
      ></footer_>
    </div>
  </transition>
</template>

<script>
import personalHeader from '@/pages/personal/personalHeader'
import personalNav from '@/pages/personal/personalNav'
import personalData from '@/pages/personal/personalData'
import personalOthers from '@/pages/personal/personalOthers'
import footer_ from '@/pages/footer'

export default {
  name: 'personal',
  components: {
    personalHeader, personalNav, personalData, personalOthers, footer_
  },
  data () {
    return {
      logoimg: require('../../assets/bar/首页.png'),
      plogocolor: '#26cbcd',
      plogoimg: require('../../assets/bar/我的-select.png'),
      logocolor: '333',
    }
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main {
  background-color: #f0f6f7;
  color: #333;
  display: flex;
  font-size: 0.9rem;
  flex-direction: column;
}
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-to {
  transform: translateX(-10px);
  opacity: 0;
}
.slide-fade-enter {
  transform: translateX(10px);
  opacity: 0;
}
</style>



