<template>
  <div id="head" :style="{ backgroundImage: bgurl }">
    <div id="skip" @click="$router.back()">
      <div><img src="./assets/箭头.png" /></div>
      <p>求助者资料</p>
    </div>
    <div id="head_s">
      <div id="head_message">
        <div id="head_img">
          <img src="./assets/头像.png" />
          <div id="sex">
            <img :src="img" />
          </div>
        </div>
        <div id="head_m">
          <p id="name"></p>
          <p id="school"></p>
        </div>
      </div>
    </div>
    <slot name="head"></slot>
  </div>
</template>

<script>
import bus from '../main'
import { volunteer, makeTeam } from '@/api/growup'

export default {
  name: 'messHead',
  data: function () {
    return {
      sex: '',
      img: '',
      cardNum: '',
      tip: '',
      bgurl: '',
      list: [],
      form: {
        name: '',
        cardNum: '',
        college: '',
        schoolId: '',
        option_value: '0',
        username: '',
        school: '',
        major: '',
        tip: '',
      }
    }
  },
  mounted () {
    bus.$on('kerwin', (data) => {
      // console.log(data)
      this.name = data.username
      if (data.cardNum == '') {
        volunteer().then(res => {
          this.list = res
          for (let index = 0; index <= this.list.length - 1; index++) {
            if (this.list[index].id == this.$router.history.current.query.Id) {
              this.form = this.list[index].role_object
              this.cardNum = this.form.cardNum
              this.tip = this.form.tip
              // console.log(this.cardNum.substr(-2, 1))
              this.changeImg()
              this.changeback()
              break;
            }
          }
        })
        return
      } else {
        this.cardNum = data.cardNum
        this.tip = data.tip
        // console.log(this.cardNum.substr(-2, 1))
        // this.changeImg()
      }
      this.changeback()
    })
  },
  methods: {
    change () {
      let sexif = this.cardNum.substr(-2, 1)
      let sexn = parseInt(sexif)
      if (sexn % 2 == 0) {
        this.sex = '0'
      } else if (sexn % 2 == 1) {
        this.sex = '1'
      }
    },
    changeImg () {
      this.change()
      // console.log(this.sex)
      if (this.sex == '0') {
        this.img = require('./assets/性别女.jpg')
      } else {
        this.img = require('./assets/性别男.png')
      }
    },
    changeback () {
      // console.log('change')
      if (this.tip == 'child') {
        this.bgurl = 'url(' + require("./assets/背景黄.png") + ')'
      }
      else if (this.tip == 'volunteer') {
        this.bgurl = 'url(' + require("./assets/背景绿.png") + ')'
      }
      else {
        this.bgurl = 'url(' + require("./assets/背景蓝.png") + ')'
      }
    }
  }
}
</script>

<style scoped>
#skip {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin-left: 2rem;
  font-size: 1.4rem;
  margin-top: 1rem;
}

#skip > div {
  height: 1.4rem;
  margin-right: 1rem;
}

#skip > div img {
  height: 100%;
}

#head {
  width: 100%;
  background-size: 100% 100%;
  position: relative;
  color: white;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

#head_s {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 1.5rem;
}

#head_message {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: -2rem;
}

#head_img {
  display: flex;
  flex-direction: row;
  z-index: 1;
}

#head_m {
  margin-left: 1.5rem;
}

#head_m > p:first-child {
  font-size: 1.2rem;
  margin-bottom: 0px;
  color: white;
}

#school {
  opacity: 0.8;
  font-size: 1rem;
  margin-bottom: 3rem;
  margin-top: 0.6rem;
}

#head_img > img {
  height: 5.3rem;
  border-radius: 50%;
}

#sex > img {
  height: 1.5rem;
  border-radius: 50%;
  margin: 4rem 0 0 -1.5rem;
}
</style>



