<template>
  <div id="data">
    <div id="data_title">
      <p>我的</p>
      <div class="arrows">
        <span><a href="#"></a></span>
        <img src="../../assets/total/灰箭头.jpg" />
      </div>
    </div>
    <div id="data_content">
      <div class="data_c">
        <p>{{ data.plans_1 }}</p>
        <p>学业帮扶次数</p>
      </div>
      <div class="data_c">
        <p>{{ data.plans_2 }}</p>
        <p>心理帮扶次数</p>
      </div>
      <div class="data_c">
        <p>{{ data.plans_3 }}</p>
        <p>生涯帮扶次数</p>
      </div>
    </div>
  </div>
</template>

<script>
import { dataget } from '@/api/growup'

export default {
  name: 'personalData',
  data () {
    return {
      data: {
        plans_1: 0,
        plans_2: 0,
        plans_3: 0,
      }
    }
  },
  mounted () {
    dataget({ pairID: JSON.parse(localStorage.getItem('info')).pair_id }).then(res => {
      this.data = res
    }
    )
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#data_content {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

#data,
.data_c {
  display: flex;
  padding: 0.5rem 0;
  flex-direction: column;
}

.arrows {
  color: #999;
  line-height: 16px;
}

.arrows img {
  height: 0.8rem;
  vertical-align: bottom;
}

#data {
  border-radius: 10px;
  align-items: center;
  margin: 1rem 1rem 0;
  background-color: #fff;
}

#data_title {
  width: 90%;
  display: flex;
  padding: 0.3rem 0;
  flex-direction: row;
  margin-bottom: 0.5rem;
  justify-content: space-between;
}

#data_content {
  width: 95%;
  padding: 1rem 0;
  border-top: 1px solid #f0f6f7;
}

.data_c {
  flex-grow: 1;
  text-align: center;
  align-items: center;
  border-left: 1px solid #f0f6f7;
}

#data_content > .data_c:first-child {
  border-left: 0 solid #f0f6f7;
}

.data_c > p:first-child {
  color: #f49b34;
  font-size: 1rem;
  margin-bottom: 1rem;
}

.data_c p {
  font-size: 0.9rem;
  margin-top: 0.3rem;
}
</style>



