import request from '@/utils/axios'

/**
 * 修改组织信息,目前由于页面问题暂时搁置修改功能
 * @param {*} data
 * @returns Promise
 */
export function orgModify(data) {

}

// 通过id获取组织详细信息
export function orgInfo(id) {
    return request({
        url: '/organization?id=' + id,
        method: 'get',
    })
}

// 通过id获取组织成员列表
export function orgMemberList(id) {
    return request({
        url: '/orgmember?id=' + id,
        method: 'get',
    })
}

// 创建组织 data: {adminDiv: '行政区id', name: '组织名称'}
export function orgCreate(data) {
    return request({
        url: '/organization/',
        method: 'post',
        data
    })
}

// 通过id切换组织状态，目前逻辑为管理员创建组织后自动切换为可用状态
export function orgState(id) {
    return request({
        url: '/orgmember/',
        method: 'put',
        // data
    })
}

// 加入组织
export function orgin(data) {
    return request({
        url: '/orgmember/',
        method: 'post',
        data: {
            inviteCode: data.inviteCode
        }
    })
}

// 解散组织
export function dismiss() {
    return request({
        url: '/organization/',
        method: 'delete',
    })
}

// 退出组织
export function resign() {
    return request({
        url: '/orgmember/',
        method: 'delete',
    })
}


