<template>
  <div id="sign">
    <logoBar></logoBar>
    <signInBlock></signInBlock>
  </div>
</template>

<script>
import logoBar from '@/pages/logoBar'
import signInBlock from '@/pages/sign/signInBlock'

export default {
  name: 'sign',
  components: {
    logoBar, signInBlock
  },
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#sign {
  padding: 24px;
}
p {
  position: absolute;
  bottom: 24px;
  left: 24px;
  color: inherit;
  font-size: 0.6rem;
}
</style>



