<template>
  <div id="another_a">
    <div class="another">
      <div>
        <div id="safe">
          <img src="../../assets/personal_others/账号安全.png" />
        </div>
        <p>账号安全</p>
      </div>
      <label>
        <div class="arrows">
          <img src="../../assets/total/灰箭头.jpg" />
        </div>
      </label>
    </div>
    <div class="another" @click="() => $router.push('/personal/advice')">
      <div>
        <div>
          <img src="../../assets/personal_others/意见反馈.png" />
        </div>
        <p>意见反馈</p>
      </div>
      <label>
        <div class="arrows">
          <img src="../../assets/total/灰箭头.jpg" />
        </div>
      </label>
    </div>

    <div
      class="another"
      v-if="tip === 'child' || tip === 'volunteer'"
      @click="drawer2 = true"
      type="primary"
      style="margin-top: 16px"
    >
      <div>
        <div>
          <img src="../../assets/personal_others/意见反馈.png" />
        </div>
        <p>加入组织</p>
      </div>
      <label>
        <div class="arrows">
          <img src="../../assets/total/灰箭头.jpg" />
        </div>
      </label>
    </div>
    <div
      class="another"
      v-if="tip === 'manager'"
      @click="drawer = true"
      type="primary"
      style="margin-top: 16px"
    >
      <div>
        <div>
          <img src="../../assets/personal_others/意见反馈.png" />
        </div>
        <p>创建组织</p>
      </div>
      <label>
        <div class="arrows">
          <img src="../../assets/total/灰箭头.jpg" />
        </div>
      </label>
    </div>

    <el-drawer
      title="创建组织"
      :visible.sync="drawer"
      :direction="direction"
      :before-close="handleClose"
      size="40%"
    >
      <div class="demo-drawer__content">
        <el-form :model="form">
          <el-form-item label="组织名称" :label-width="formLabelWidth">
            <el-input
              v-model="form.name"
              style="width: 80%"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="组织区域" :label-width="formLabelWidth">
            <el-select v-model="form.adminDiv" placeholder="请选择活动区域">
              <el-option label="山西省太原市小店区" value="1"></el-option>
              <el-option label="北京市" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button type="primary" @click="submitForm()" :loading="loading">{{
            loading ? "提交中 ..." : "确 定"
          }}</el-button>
        </div>
      </div>
    </el-drawer>
    <el-drawer
      title="加入组织"
      :visible.sync="drawer2"
      :direction="direction"
      :before-close="handleClose"
      size="40%"
    >
      <div class="demo-drawer__content">
        <el-form :model="form2">
          <el-form-item label="邀请码" :label-width="formLabelWidth">
            <el-input
              v-model="form2.code"
              style="width: 80%"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button type="primary" @click="submitForm2()" :loading="loading">{{
            loading ? "提交中 ..." : "确 定"
          }}</el-button>
        </div>
      </div>
    </el-drawer>

    <div class="another" @click="() => logout()">
      <div>
        <div>
          <img src="../../assets/personal_others/意见反馈.png" />
        </div>
        <p>退出登录</p>
      </div>
      <label>
        <div class="arrows">
          <img src="../../assets/total/灰箭头.jpg" />
        </div>
      </label>
    </div>
  </div>
</template>

<script>
import { logout, accounts } from '@/api/account'
import { orgCreate, orgState, orgin } from '@/api/org'
import { Toast, MessageBox } from 'mint-ui';

export default {
  name: 'personalOthers',
  data () {
    return {
      drawer: false,
      drawer2: false,
      direction: 'btt',
      tip: JSON.parse(localStorage.getItem('info')).tip || null,
      loading: false,
      form: {
        name: '测试组织',
        adminDiv: ''
      },
      form2: {
        code: '',
      },
      formLabelWidth: '80px',
    }
  },
  methods: {
    logout () {
      // todo t提示是否退出，确认以后再退出
      MessageBox.confirm('Are you sure?').then(action => {
        logout().then(res => {
          localStorage.clear()
          this.$router.push('/home')
        }).catch(err => {
          localStorage.clear()
          this.$router.push('/home')
        })
        return
      });
    },
    // 拉起组织创建的弹框后点击背景区域调用该函数关闭弹框
    handleClose (done) {
      // 判断当前是否正在进行提交组织信息的操作
      if (this.loading) {
        return;
      }
      // 关闭弹框
      done();
      return
    },
    // 提交创建组织的信息
    submitForm () {
      this.loading = true
      let that = this
      orgCreate(this.form).then(res => {
        // 组织id
        console.log(res.id)
        if (res.isActive === "0") {
          orgState().then(res => {
            console.log(res)
            that.loading = false
            MessageBox.alert('邀请码' + res.inviteCode).then(action => {
              window.location.reload()
            });
          })
        }
      }).catch(err => {
        that.loading = false
        // console.log(err)
        Toast('创建失败')
      })
    },
    //加入组织
    submitForm2 () {
      if (this.form2.code == '') {
        Toast("未填写邀请码")
        return 0
      } else {
        orgin({'inviteCode': this.form2.code}).then(res => {
          // 加入组织成功，刷新页面重新获取数据
          window.location.reload()
        }).catch(err => {
          Toast('加入失败')
        })
        // Toast("加入成功")
      }
    },

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#another_a {
  display: flex;
  padding: 0.5rem 0;
  flex-direction: column;
}

.arrows {
  color: #999;
  line-height: 16px;
}

.arrows img {
  height: 0.8rem;
  vertical-align: bottom;
}

label {
  display: flex;
  font-size: 0.8rem;
  flex-direction: row;
  align-items: center;
}

label img {
  padding: 0 0.5rem 0 0.9rem;
}

.another {
  width: 90%;
  height: 3rem;
  display: flex;
  margin-top: 1rem;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.another > div {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
}

#another_a {
  width: 100%;
  padding: 0 0 1rem;
  margin: 1rem 0 8rem;
  align-items: center;
  background-color: #fff;
}

.another > div > div {
  width: 3rem;
  margin-right: 1rem;
}

.another > div > div > img {
  height: 3rem;
}
</style>



