<template>
  <div id="box">
    <header style="border-bottom: 1px solid #f0f6f7">
      <p style="font-size: 1.3rem; margin: 5px">{{ title }}</p>
    </header>
    <div v-if="type === 1">
      <volunteerItems :list="list"></volunteerItems>
    </div>
    <div v-else-if="type === 2">
      <!-- 帮扶大事件 -->
       <helpEvent></helpEvent>
    </div>
    <div v-else-if="type === 3">
      无结对信息
    </div>
    <footer_
      :logo="logoimg"
      :lcolor="logocolor"
      :plogo="plogoimg"
      :plcolor="plogocolor"
    ></footer_>
  </div>
</template>

<script>
import headWithBack from '@/pages/headWithBack'
import helpEvent from '@/pages/helpEvent/helpEvent'
import volunteerItems from '@/components/volunteer/volunteerItems'
import footer_ from '@/pages/footer'
import { volunteer, getPlan } from '@/api/growup'

export default {
  name: 'volunteer',
  components: { headWithBack, footer_, volunteerItems, helpEvent },
  data: function () {
    return {
      title: '志愿者列表',
      type: 1, // 1 志愿者列表 2 大事件 3 无信息
      logoimg: require('../../assets/bar/首页.png'),
      logocolor: '333',
      plogoimg: require('../../assets/bar/我的.png'),
      plogocolor: '333',
      list: [],
      sex: '',
      cardNum: '',
    }
  },
  mounted () {
    let info = JSON.parse(localStorage.getItem('info'))
    // 判断当前页面显示内容
    if (info.pair_id) { // 帮扶大事件列表
      this.title = "帮扶大事件"
      this.type = 2
      getPlan(info.pair_id).then(res => {
        this.list = res
      })
    }
    else if (info.tip === 'volunteer' && info.isPaired == '0') { // 未结对志愿者
      this.title = '当前暂无结对'
      this.type = 3
    } else if (info.tip === 'child' && info.isPaired == '0') { // 未结对儿童
      volunteer().then(res => { // 获取符合条件的志愿者列表
        this.list = res
      })
    }
  },
  methods: {

  }
}
</script>

<style lang="css" scoped>
#box {
  height: 100%;
  width: 100vw;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  margin: 0;
  background-color: white;
  /* 实现顶部固定更好 */
}
</style>



