<template>
  <div id="box">
    <headWithBack title="通知公告" :showTitle="true"></headWithBack>
    <div v-if="list.length>0">
      <div v-for="item in list" v-bind:key="item.id">
         <messListn :info=item></messListn>
      </div>
    </div>
  </div>
</template>

<script>
import headWithBack from '@/pages/headWithBack'
import messListn from '@/components/messListn'
import {getNotice} from '@/api/growup'

export default {
  name: 'notice',
  components: { headWithBack, messListn },
  data: function () {
    return {
      list: []
    }
  },
  mounted () {
    getNotice().then(res => {
      this.list = res
    })
  }
}
</script>

<style lang="css" scoped>
</style>



