<template>
  <div id="nav">
    <div class="nav_s">
      <div
        class="nav-img"
        @click="
          () => $router.push({ path: '/personal/org', query: { id: id } })
        "
      >
        <img src="../../assets/personal_nav/我的组织.png" />
      </div>
      <p>我的组织</p>
    </div>
    <div class="nav_s">
      <div class="nav-img" @click="() => $router.push('helpCenter')">
        <img src="../../assets/personal_nav/求助请求.jpg" />
      </div>
      <p>求助请求</p>
    </div>
    <div class="nav_s">
      <div class="nav-img" @click="() => $router.push('notice')">
        <img src="../../assets/personal_nav/通知公告.jpg" />
      </div>
      <p>通知公告</p>
    </div>
  </div>
</template>

<script>
import bus from '@/main'

export default {
  name: 'personalNav',
  data () {
    return {
      id: 0
    }
  },
  mounted () {
    // 接收监听信号 org_id，该信号由personalHeader发出
    bus.$on('org_id', (id) => {
      this.id = id
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#nav {
  border-radius: 10px;
  align-items: center;
  margin: 1rem 1rem 0;
  font-weight: lighter;
  padding-bottom: 1rem;
  background-color: #fff;
}

#nav img {
  height: 100%;
  margin: 0 auto;
  display: block;
}

.nav-img {
  height: 2rem;
  margin: 0.5rem 0;
}

#nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.nav_s {
  display: flex;
  padding: 0.5rem 0;
  flex-direction: column;
}
</style>



