<template>
  <transition  name="slide-fade" mode="out-in" appear>
    <div id="box">
      <orgDetailHead></orgDetailHead>
      <orgDetailMain></orgDetailMain>
    </div>
  </transition>
</template>

<script>

import orgDetailHead from '@/pages/organization/orgDetailHead'
import orgDetailMain from '@/pages/organization/orgDetailMain'

export default {
  name: 'orgDetail',
  components: {
    orgDetailHead, orgDetailMain
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
#box {
  margin: 0;
  padding: 0;
}
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-to {
  transform: translateX(-10px);
  opacity: 0;
}
.slide-fade-enter {
  transform: translateX(10px);
  opacity: 0;
}
</style>



