<template>
  <transition name="slide-fade" mode="out-in">
    <div id="main">
      <div id="m_tit">
        <p>文章</p>
        <!-- <p><a href="#">更多</a></p> -->
      </div>
      <div id="content" v-if="list.length > 0">
        <div
          class="message"
          v-for="item in list"
          v-bind:key="item.index"
          @click="
            () => $router.push({ name: 'detail', query: { Id: item.id } })
          "
        >
          <div class="img"><img src="../../assets/home/花.png" /></div>
          <div class="p">
            <p>
              [{{ item.tags }}]<span>{{ item.title.substring(0, 10) }}</span>
            </p>
            <p class="lighter">{{ item.abstract.substring(0, 25) }}...</p>
          </div>
          <!-- <div class="close">
          <button>
            <img src="../../assets/home/关闭1.png" />
          </button>
        </div> -->
        </div>
        <div style="margin-top: 10px"></div>
        <el-pagination
          background
          :pager-count="5"
          layout="pager"
          :total="total"
          :page-size="10"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
    </div>
  </transition>
</template>

<script>
import { article } from '@/api/home'
export default {
  name: 'homeArticle',
  data () {
    return {
      list: [],
      start: 1,
      total: 100
    }
  },
  mounted () {
    article(this.start).then(res => {
      // console.log(res)
      this.list = res.articles
      this.total = res.total
      console.log(this.total)
    })
  },
  methods: {
    handleCurrentChange (val) {
      this.start = 10 * (val - 1)
      article(this.start).then(res => {
        // console.log(res)
        this.list = res.articles
      })
    }
  }
}
</script>

<style scoped>
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-to {
  transform: translateX(-10px);
  opacity: 0;
}
.slide-fade-enter {
  transform: translateX(10px);
  opacity: 0;
}
button {
  outline: none;
  border-style: none;
}

a {
  text-decoration: none;
}

input {
  outline: none;
  border: none;
}
#main {
  display: flex;
  flex-direction: column;
  /* width: 95%; */
  margin: 0 1rem;
}

#m_tit {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 1.2rem;
  align-items: center;
  padding: 0.3rem 0;
}

a {
  font-size: 1rem;
  color: #888888;
  margin-right: 1rem;
}

#content {
  display: flex;
  flex-direction: column;
  width: 95%;
  margin-bottom: 7rem;
}

.message {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 1rem;
  align-items: center;
}

.message .lighter {
  font-weight: lighter;
}

.message > .img img {
  height: 5rem;
}

.close {
  margin: 4rem 0 0 0;
}

.close button {
  background-color: #ffffff;
}

.close img {
  height: 1rem;
}

.p {
  color: #888888;
  font-size: 0.8rem;
  margin-left: 1rem;
  height: 100%;
  text-align: left;
  flex-grow: 1;
}

.p > p:first-child {
  font-size: 1rem;
  color: rgb(155, 44, 44);
  font-weight: bold;
}

.p span {
  color: black;
  margin-left: 3px;
  font-weight: normal;
}
</style>



