<template>
  <div class="content">
    <headWithBack title="" :showTitle="true"></headWithBack>
    <div id="article" v-if="detail">
      <input
        class="title"
        type="text"
        :readOnly="isReadonly"
        v-model="detail.title"
      />
      <div class="info">
        <i class="el-icon-s-order"></i>
        <span>{{ detail.achievement }}</span>
        <span class="date">{{ detail.date }}</span>
      </div>
      <p v-html="detail.plan" style="text-align:left">
      </p>
    </div>
    <p v-else>{{ info }}</p>
    <input
      type="submit"
      v-if="detail"
      class="yujian-input"
      :value="butt"
      @click="
        () => {
          drawer = true;
          asyncForm();
        }
      "
    />
    <el-drawer
      title="修改帮扶"
      :visible.sync="drawer"
      :direction="direction"
      :before-close="handleClose"
      :destroy-on-close="true"
      size="90%"
    >
      <div class="demo-drawer__content">
        <el-form :model="form">
          <el-form-item label="标题*" :label-width="formLabelWidth">
            <el-input
              v-model="form.title"
              style="width: 80%"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="内容*" :label-width="formLabelWidth">
            <el-input
              type="textarea"
              :autosize="{ minRows: 10 }"
              v-model="form.plan"
              style="width: 80%"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="成就" :label-width="formLabelWidth">
            <el-input
              v-model="form.achievement"
              style="width: 80%"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="帮扶类型" :label-width="formLabelWidth">
            <el-select v-model="form.type" placeholder="请选择帮扶类型">
              <el-option label="学业帮扶" value="1"></el-option>
              <el-option label="心理帮扶" value="2"></el-option>
              <el-option label="生涯帮扶" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button type="primary" @click="submitForm()" :loading="loading">{{
            loading ? "提交中 ..." : "确 定"
          }}</el-button>
          <el-button type="danger" @click="deletePlan()" :loading="loading">{{
            loading ? "提交中 ..." : "删 除"
          }}</el-button>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import headWithBack from '@/pages/headWithBack'
import { helpDetail, helpchange, helpDelete } from '@/api/help'
import { Toast } from 'mint-ui';

export default {
  name: 'event',
  components: { headWithBack },
  data: function () {
    return {
      detail: null,
      formLabelWidth: '80px',
      direction: 'btt', // 抽屉出现方向  从底部向上
      info: '加载中...(ง •_•)ง',
      data: '',
      butt: '编辑',
      id: '',
      drawer: false,
      loading: false,
      isReadonly: true,
      content: '',
      title: '',
      achievement: '',
      // 抽屉表单数据
      form: {
        title: '',
        planID: '',
        type: "",
        achievement: '',
        pair: JSON.parse(localStorage.getItem('info')).pair_id
      },
    }
  },
  mounted () {
    this.id = this.$route.query.id
    helpDetail(this.$route.query.id).then(res => {
      this.detail = res
    }).catch(err => {
      Toast("加载失败")
    })
  },
  methods: {
    // 将form中的数据同步至detail
    asyncDetail () {
      this.detail = {
        title: this.form.title,
        id: this.form.planID,
        type: this.form.type,
        plan: this.form.plan,
        achievement: this.form.achievement
      }
    },
    // 将detail中的数据同步至form
    asyncForm () {
      this.form = {
        title: this.detail.title,
        planID: this.detail.id,
        type: this.detail.type,
        plan: this.detail.plan,
        achievement: this.detail.achievement
      }
    },
    // 拉起帮扶创建的弹框后点击背景区域调用该函数关闭弹框
    handleClose (done) {
      // 判断当前是否正在进行提交组织信息的操作
      if (this.loading) {
        return;
      }
      // 关闭弹框
      done();
      this.form.title = ''
      this.form.plan = ''
      return
    },
    submitForm () {
      if (confirm('确认修改内容吗') == true) {
        this.loading = true
        helpchange(this.form).then(res => {
          Toast('修改成功')
          this.loading = false
          this.drawer = false
          this.asyncDetail()
        }).catch(error => {
          Toast('内容修改失败,请重试')
          this.loading = false
          this.drawer = false
        })
      }
    },
    // 删除帮扶
    deletePlan () {
      if (confirm('确认删除删除帮扶吗？') == true) {
        helpDelete(this.form.planID).then(res => {
          Toast("删除成功")
          this.$router.go(-1)
        }).catch(err => {
          this.drawer = false;
          Toast("网络失败，请重试")
        })
      }
    }
  }
}
</script>

<style lang="css" scoped>
input {
  border: transparent;
}

.content > div {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  /* align-self: center; */
}

.title {
  font-size: 2rem;
  /* font-weight: bold; */
}

.info {
  margin: 1rem 0;
  text-align: left;
}

.info .date {
  float: right;
}

#article {
  padding: 1rem;
  /* margin-top: 2rem; */
  font-size: 1.2rem;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

#article > p {
  line-height: 1.8rem;
}

#article > img {
  margin-top: 3rem;
  margin-bottom: 2rem;
}

.yujian-input[type="submit"] {
  width: 80%;
  background-color: #2599d2;
  color: white;
  font-size: 1.2rem;
  line-height: 1.2rem;
  margin-bottom: 3rem;
  margin-top: 3rem;
  box-sizing: border-box;
  border: none;
  border-radius: 25px;
  margin: 0.7rem 0;
  padding: 10px 20px;
  font-size: 1rem;
  margin-right: 0px;
}
</style>


