<template>
<transition name="slide-fade" mode="out-in">
  <div class="content">
    <headWithBack></headWithBack>
    <div id="head" v-if="detail">
      <p class="title">{{ detail.title }}</p>
      <span class="categ"
        >[{{ detail.categories[0].name }}] {{ detail.authors[0].name }}</span
      >
      <p class="timeline">{{ detail.date }}</p>
    </div>
    <div v-if="detail">
      <div id="article" v-html="detail.body"></div>
    </div>
    <p v-else>{{ info }}</p>
  </div>
</transition>
</template>

<script>
import { articleDetail } from '@/api/home'
import headWithBack from '@/pages/headWithBack'
export default {
  name: 'articleDetail',
  components: { headWithBack },
  data: function () {
    return {
      detail: null,
      info: '加载中...(ง •_•)ง'
    }
  },
  mounted () {
    articleDetail(this.$router.history.current.query.Id).then(res => {
      this.detail = res
    })
  }
}
</script>

<style lang="css" scoped>
@import "../assets/normalize.css";
.content > div {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  /* align-self: center; */
}

#head p,
#head span {
  text-align: left;
}

#head .title {
  padding: 1rem;
  font-size: 1.5rem;
  font-weight: 500;
}
#head .timeline,
.categ,
.author {
  padding: 0 1rem 1rem 1rem;
  font-size: 0.8rem;
  margin-top: 2px;
  font-weight: lighter;
}
#article {
  padding: 1rem;
  /* margin-top: 2rem; */
  font-size: 1.2rem;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

#article > p {
  line-height: 1.8rem;
}

#article > img {
  margin-top: 3rem;
  margin-bottom: 2rem;
}

.yujian-input[type="submit"] {
  width: 80%;
  background-color: #2599d2;
  color: white;
  font-size: 1.2rem;
  line-height: 1.2rem;
  margin-bottom: 3rem;
  margin-top: 3rem;
  box-sizing: border-box;
  border: none;
  border-radius: 25px;
  margin: 0.7rem 0;
  padding: 10px 20px;
  font-size: 1rem;
  margin-right: 0px;
}
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-to {
  transform: translateX(-10px);
  opacity: 0;
}
.slide-fade-enter {
  transform: translateX(10px);
  opacity: 0;
}
</style>


