<template>
  <div id="head">
    <div @click="()=> $router.back()">
      <div><img src="../../assets/org/箭头.png" /></div>
      <div></div>
    </div>
    <div id="message">
      <div><img src="../../assets/org/logo.png" /></div>
      <div>
        <p>山西省&nbsp;太原市</p>
        <p style="font-size: 1.2rem; font-weight: bold">太原理工大学</p>
        <p>遇见世界的美好</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'orgDetailHead',
  data () {
    return {
    }
  }
}
</script>

<style scoped>
#head {
  background-color: #2599d2;
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
  width: 100%;
}

#head > div:first-child {
  height: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 0.5rem;
}

#head div img {
  height: 100%;
}

#message {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 5%;
  color: white;
}

#message > div:first-child {
  height: 6rem;
}

#message div img {
  height: 100%;
}

#message div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#message div p {
  margin-top: 0.5rem;
  font-size: 0.8rem;
}
</style>



