<template>
  <div>
    <transition name="slide-fade" mode="out-in">
      <div v-if="page === 'username'" class="username_page" key="username_page">
        <div class="title">
          <p>创建账户</p>
        </div>
        <div class="form">
          <input
            class="yujian-input"
            name="username"
            type="text"
            id="phone"
            placeholder="手机号码"
            v-model="username"
          />
          <p
            style="
              color: red;
              font-weight: 100;
              font-size: 14px;
              text-align: left;
            "
          >
            {{ msgPhone }}
          </p>
          <div class="deal">
            <label>
              <input
                v-model="agreement"
                type="radio"
                name="agreement"
                value="1"
              />
              <p @click="agreement()">《遇见服务协议》</p>
            </label>
            <div>
              <span style="float: right"
                >已有帐户？
                <a href="/#/signin" class="yujian-jump">去登录！</a>
              </span>
            </div>
          </div>
          <input
            type="button"
            class="yujian-input"
            value="下一步"
            v-on:click="switch_page('password')"
          />
        </div>
      </div>
      <div v-else-if="page === 'password'" key="password_page">
        <div class="username">
          <button class="back_logo_button" v-on:click="back('username')">
            <img class="back_logo" src="../../assets/total/灰箭头.jpg" />
          </button>
          <p>{{ username }}</p>
        </div>
        <div class="title">
          <p>输入密码</p>
        </div>
        <div class="form">
          <input
            class="yujian-input"
            name="password"
            type="password"
            id="passwd"
            placeholder="密码"
            v-model="password"
          />
          <p
            style="
              color: red;
              font-weight: 100;
              font-size: 14px;
              text-align: left;
            "
          >
            {{ msgpassword }}
          </p>
          <input
            type="button"
            class="yujian-input"
            value="选择身份"
            v-on:click="switch_page('role_choose')"
          />
        </div>
      </div>
      <div v-else-if="page === 'role_choose'" key="role_choose">
        <div class="username">
          <button class="back_logo_button" v-on:click="switch_page('password')">
            <img class="back_logo" src="../../assets/total/灰箭头.jpg" />
          </button>
          <p>{{ username }}</p>
        </div>
        <div class="title">
          <p>选择身份</p>
        </div>
        <div
          class="content"
          v-on:click="
            choose_role('child');
            switch_page('personal_info_page');
          "
        >
          <div>
            <p>我是儿童</p>
            <p>Children&nbsp;Entrance</p>
          </div>
          <div class="img">
            <a href=""><img src="../../assets/sign/箭头小.png" /></a>
          </div>
        </div>
        <div
          class="content"
          v-on:click="
            choose_role('volunteer');
            switch_page('personal_info_page');
          "
        >
          <div>
            <p>我是志愿者</p>
            <p>Volunteers&nbsp;Entrance</p>
          </div>
          <div class="img">
            <img src="../../assets/sign/箭头小.png" />
          </div>
        </div>
        <div
          class="content"
          v-on:click="
            choose_role('manager');
            switch_page('personal_info_page');
          "
        >
          <div>
            <p>我是管理员</p>
            <p>Administrators&nbsp;Entrance</p>
          </div>
          <div class="img">
            <img src="../../assets/sign/箭头小.png" />
          </div>
        </div>
      </div>
      <div v-else key="personal_info_page">
        <div class="username">
          <button
            class="back_logo_button"
            v-on:click="switch_page('role_choose')"
          >
            <img class="back_logo" src="../../assets/total/灰箭头.jpg" />
          </button>
          <p>{{ username }}</p>
        </div>
        <div class="title">
          <p>完善个人信息</p>
        </div>
        <div class="form">
          <input
            class="yujian-input"
            name="name"
            type="text"
            placeholder="姓名"
            v-model="name"
          />
          <input
            class="yujian-input"
            name="cardNum"
            type="text"
            placeholder="身份证号"
            v-model="cardNum"
          />
          <input
            class="yujian-input"
            name="schoolId"
            type="text"
            placeholder="学号"
            v-model="schoolId"
            v-if="tip === 'volunteer'"
          />
          <input
            class="yujian-input"
            name="college"
            type="text"
            placeholder="学院"
            v-model="college"
            v-if="tip === 'volunteer'"
          />
          <input
            class="yujian-input"
            name="major"
            type="text"
            placeholder="专业"
            v-model="major"
            v-if="tip === 'volunteer'"
          />
          <input
            class="yujian-input"
            name="school"
            type="text"
            placeholder="学校"
            v-model="school"
            v-if="tip === 'child'"
          />
          <p
            style="
              color: red;
              font-weight: 100;
              font-size: 14px;
              text-align: left;
            "
          >
            {{ msg }}
          </p>
          <input
            type="button"
            class="yujian-input"
            value="提交"
            @click="submit()"
          />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
// import axios from 'axios'
import { register, login, accounts } from '@/api/account'
import { Toast } from 'mint-ui';
export default {
  name: 'sign_up',
  data () {
    return {
      username: '',
      password: '',
      page: 'username',
      tip: '',
      name: '',
      cardNum: '',
      schoolId: '',
      college: '',
      major: '',
      school: '',
      msg: '',
      msgPhone: '',
      msgpassword: '',
      agreement: ''
    }
  },
  mounted: {
  },
  methods: {
    switch_page: function (page) {
      switch (this.page) {
        case 'username':
          if (this.username == '') {
            this.msgPhone = '请填写手机号'
            return 0;
          } else if (this.agreement != '1') {
            this.msgPhone = '请选择协议'
            return 0;
          } else { this.page = page };
          break;
        case 'password':
          if (this.password == '') {
            this.msgpassword = '请填写密码'
            return 0
          } else { this.page = page };
          break;
        case 'role_choose':
          this.page = page;
          break;
      }
    },
    agreement: function () {
      this.$router.push('agreement')
    },
    choose_role: function (tip) {
      this.tip = tip
    },
    back: function (page) {
      this.page = page;
    },
    submit () {
      // console.log(this.tip)
      switch (this.tip) {
        case 'child':
          if (this.tip == '' || this.name == '' || this.cardNum == '' || this.school == '') {
            this.msg = '请完整填写信息'
            return 0
          }
          break;
        case 'volunteer':
          if (this.tip == '' || this.name == '' || this.schoolId == '' || this.college == '' || this.major == '') {
            this.msg = '请完整填写信息'
            return 0
          }
          break;
        case 'manager':
          if (this.tip == '' || this.name == '' || this.cardNum == '') {
            this.msg = '请完整填写信息'
            return 0
          }
          break;
      }
      let data = {}
      if (this.tip == 'child') {
        data = {
          username: this.username,
          password: this.password,
          role_object: {
            'tip': this.tip,
            'name': this.name,
            'cardNum': this.cardNum,
            'school': this.school,
          }
        }
      }
      else if (this.tip == 'volunteer') {
        data = {
          username: this.username,
          password: this.password,
          role_object: {
            'tip': this.tip,
            'name': this.name,
            'cardNum': this.cardNum,
            'schoolId': this.schoolId,
            'college': this.college,
            'major': this.major
          }
        }
      }
      else if (this.tip == 'manager') {
        data = {
          username: this.username,
          password: this.password,
          role_object: {
            'tip': this.tip,
            'name': this.name,
            'cardNum': this.cardNum,
          }
        }
      }
      register(data).then(result => {
        // 注册成功自动登录
        login({ 'username': this.username, 'password': this.password })
          .then(res => {
            window.localStorage.clear()
            window.localStorage.setItem('token', res.token)
            // 获取用户数据
            accounts().then(r => {
              this.name = r.role_object.name
              this.organization = r.organization
              localStorage.setItem('info', JSON.stringify(r.role_object))
              Toast({
                message: '登录成功，即将跳转',
                position: 'bottom',
                duration: 1000
              });
              setTimeout(() => {   //设置延迟执行
                this.$router.push('/');
              }, 3000);
            })
          })
      })
        .catch(error => {
          var object = error.response.data
          for (var index in object) {
            // console.log(index);
            // console.log(object[index])
            this.msg = object[index][0]
          }
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.back_logo_button {
  margin-right: 0.5rem;
  background-color: white;
  border: none;
}

.back_logo_button:focus {
  outline: none;
}

.deal > label {
  display: flex;
  flex-direction: row;
}
.back_logo {
  transform: rotateY(180deg);
  height: 14px;
}

.title {
  margin-bottom: 12px;
  margin-top: 16px;
  font-weight: bold;
  font-size: 28px;
  text-align: left;
}

.username {
  height: 24px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
  font-weight: 400;
  font-size: 0.9375rem;
  margin-top: 16px;
  color: #1b1b1b;
}

.username p {
  display: inline;
}

.deal {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 1rem 0;
  font-size: 0.6rem;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-bottom: 40%;
}

.yujian-input[type="text"],
.yujian-input[type="password"] {
  box-sizing: border-box;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom-color: rgba(0, 0, 0, 0.8);
  border-bottom-width: 1px;
  border-radius: 0;
  margin: 0.8rem 0;
  height: 36px;
  font-size: 1.02rem;
}

.yujian-input[type="text"]:focus,
.yujian-input[type="password"]:focus,
.yujian-input[type="button"]:focus {
  outline: none;
}

.yujian-input[type="button"] {
  border: none;
  min-width: 108px;
  width: fit-content;
  padding: 6px 12px;
  background-color: #0067b8;
  color: white;
  font-size: inherit;
  line-height: 1.2rem;
  margin-left: auto;
  margin-top: 0.8rem;
}

.yujian-jump {
  color: #0a4c54;
  transition: 0.5s;
  cursor: pointer;
}

.yujian-jump:hover {
  opacity: 0.3;
}

.content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #f8f8fa;
  border-radius: 20px;
  margin: 1rem 0;
  align-items: center;
  padding: 1.2rem 1.8rem;
}

.content p:first-child {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  text-align: left;
}

.content p {
  color: black;
  font-size: 0.8rem;
}

.img {
  height: 1.3rem;
}

.content img {
  height: 100%;
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-to {
  transform: translateX(-10px);
  opacity: 0;
}
.slide-fade-enter {
  transform: translateX(10px);
  opacity: 0;
}
</style>



