<template>
  <div id="main" v-if="list.length > 0">
    <div class="message" v-for="item in list" v-bind:key="item.index">
      <div class="head_png">
        <img src="../../assets/org/1.png" />
      </div>
      <div id="mess">
        <p>
          {{ item.role_object.name }}<span>（{{ item.username }}）</span>
        </p>
        <div>
          <div
            class="sex"
            :style="{ backgroundColor: sexcolor(item.role_object.cardNum) }"
          >
            <img :src="gudge(item.role_object.cardNum)" /><span>21</span>
          </div>
          <div class="ad">
            <span>在{{ item.role_object.adminDiv }}</span>
          </div>
          <div class="la"><span>贴心</span></div>
        </div>
        <p id="s">在读院校：<span>太原理工大学</span></p>
      </div>
      <div id="butt">
        <button
          :style="{ backgroundColor: sexcolor(item.role_object.cardNum) }"
          @click="
            () => $router.push({ name: 'vltdetail', query: { Id: item.id } })
          "
        >
          <p>了解TA</p>
          <div>
            <img :src="arrows(item.role_object.cardNum)" />
          </div>
        </button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'volunteerItems',
  props: ['list'],
  data: function () {
    return {

    }
  },
  mounted () {

  },
  methods: {
    gudge (cardNum) {
      let sexif = cardNum.substr(-2, 1)
      let sexn = parseInt(sexif)
      let seximg
      if (sexn % 2 == 0) {
        this.sex = '0'
        seximg = require('../../assets/org/woman.png')
      } else if (sexn % 2 == 1) {
        this.sex = '1'
        seximg = require('../../assets/org/man.png')
      }
      return seximg
    },
    arrows (cardNum) {
      let sexif = cardNum.substr(-2, 1)
      let sexn = parseInt(sexif)
      let arrimg
      if (sexn % 2 == 0) {
        this.sex = '0'
        arrimg = require('../../assets/volunteer/箭头.png')
      } else if (sexn % 2 == 1) {
        this.sex = '1'
        arrimg = require('../../assets/volunteer/箭头1.png')
      }
      return arrimg
    },
    sexcolor (cardNum) {
      let sexif = cardNum.substr(-2, 1)
      let sexn = parseInt(sexif)
      let color
      if (sexn % 2 == 0) {
        this.sex = '0'
        color = '#fe80ca'
      } else if (sexn % 2 == 1) {
        this.sex = '1'
        color = '#26cbcd'
      }
      return color
    }
  }
}
</script>


<style scoped>
#main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 7rem;
}

.message {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  height: 7rem;
  border-bottom: 1px solid #f3f3f3;
}

.head_png img {
  width: 4.5rem;
  height: 4.5rem;
}

#mess {
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 0.5rem;
}

#mess > div {
  display: flex;
  flex-direction: row;
  margin: 0.2rem 0;
}
#mess > p {
  display: flex;
  flex-direction: row;
}
.sex {
  height: 1rem;
  border-radius: 5px;
  color: white;
  padding: 0.1rem 0.2rem;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  margin-left: 0.3rem;
}
.ad,
.la {
  height: 1rem;
  background-color: #fe80ca;
  border-radius: 5px;
  color: white;
  padding: 0.1rem 0.2rem;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  margin-left: 0.3rem;
}

.sex > img {
  height: 100%;
}

.ad {
  background-color: #00c9fb;
}

.la {
  background-color: #fec519;
}

#mess > p:first-child > span {
  color: #2599d2;
  font-size: 0.9rem;
}

#s {
  font-size: 0.9rem;
  color: #999999;
}
button {
  padding: 0.2rem 0.2rem 0.2rem 0.8rem;
  background-color: #fe80ca;
  border-radius: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 0.8rem;
  color: white;
  display: flex;
  align-items: center;
  outline: none;
  border: none;
}

button img {
  background-color: white;
  border-radius: 50%;
  width: 30%;
  padding: 0.4rem 0.4rem;
  margin-top: 0.1rem;
}
</style>



