<template>
  <transition name="slide-fade" mode="out-in" appear>
    <div id="main">
      <headWithBack></headWithBack>
      <organizationMsg></organizationMsg>
      <organizationMem></organizationMem>
    </div>
  </transition>
</template>

<script>
import headWithBack from '@/pages/headWithBack'
import organizationMsg from '@/pages/organization/organizationMsg'
import organizationMem from '@/pages/organization/organizationMem'

export default {
  name: 'org',
  components: {
    headWithBack, organizationMsg, organizationMem
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
#main {
  margin: 0;
  padding: 0;
  background-color: #f0f6f7;
  overflow-x: hidden;
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-to {
  transform: translateX(-10px);
  opacity: 0;
}
.slide-fade-enter {
  transform: translateX(10px);
  opacity: 0;
}
</style>




