<template>
  <section class="main">
    <header>
      <div id="time">
        <div><img src="./assets/时间串.png" /></div>
        <p>时间串</p>
      </div>
    </header>
    
    <div class="item-list">
      <div id="add" class="item">
        <div
          @click="drawer = true"
          type="primary"
          class="img-box"
          id="add_i"
        ></div>
        <div class="content"></div>
      </div>
      <div
        class="item"
        v-for="item in list"
        v-bind:key="item.index"
        :data-date="item.date.substring(5)"
      >
        <div class="img-box"><img src="./assets/myevent-img.png" alt="" /></div>
        <div class="content">
          <p class="title">{{ item.title }}</p>
          <div class="content-bottom">
            <span class="id"
              >类别：<span>{{ typeList[item.type - 1] }}</span></span
            >
            <button
              @click="
                () => $router.push({ path: 'event', query: { id: item.id } })
              "
            >
              详情
            </button>
          </div>
        </div>
      </div>
    </div>

<!-- 创建帮扶抽屉弹框 -->
    <el-drawer
      title="创建帮扶"
      :visible.sync="drawer"
      :direction="direction"
      :before-close="handleClose"
      :destroy-on-close="true"
      size="90%"
    >
      <div class="demo-drawer__content">
        <el-form :model="form">
          <el-form-item label="标题*" :label-width="formLabelWidth">
            <el-input
              v-model="form.title"
              style="width: 80%"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="内容*" :label-width="formLabelWidth">
            <el-input
              type="textarea"
              :autosize="{ minRows: 10 }"
              v-model="form.plan"
              style="width: 80%"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="成就" :label-width="formLabelWidth">
            <el-input
              v-model="form.achievement"
              style="width: 80%"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="帮扶类型" :label-width="formLabelWidth">
            <el-select v-model="form.type" placeholder="请选择帮扶类型">
              <el-option label="学业帮扶" value="1"></el-option>
              <el-option label="心理帮扶" value="2"></el-option>
              <el-option label="生涯帮扶" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button type="primary" @click="submitForm()" :loading="loading">{{
            loading ? "提交中 ..." : "确 定"
          }}</el-button>
        </div>
      </div>
    </el-drawer>
  </section>
</template>

<script>
import { helplist, helpCreate } from '@/api/help'

export default {
  name: 'timeline',
  data: function () {
    return {
      id: JSON.parse(localStorage.getItem('info')).pair_id,
      drawer: false,// 抽屉属性
      direction: 'btt', // 抽屉出现方向  从底部向上
      loading: false, // loading状态标识符
      formLabelWidth: '80px',
      typeList: ["学业帮扶", "心理帮扶", "生涯帮扶"],// 根据帮扶事件type取值
      list: [],
      form: {
        title: '',
        plan: '',
        type: "1",
        achievement: '成就',
        pair: JSON.parse(localStorage.getItem('info')).pair_id
      },
    }
  },
  mounted () {
    // 加载帮扶大事记列表
    helplist(this.id).then(res => {
      this.list = res
    })
  },
  methods: {
    // 跳转详情
    details (id) {
      this.$router.push({ path: 'event', query: { id } })
    },
    // 拉起帮扶创建的弹框后点击背景区域调用该函数关闭弹框
    handleClose (done) {
      // 判断当前是否正在进行提交组织信息的操作
      if (this.loading) {
        return;
      }
      // 关闭弹框
      done();
      this.form.title = ''
      this.form.plan = ''
      return
    },
    // 提交创建帮扶的信息
    submitForm () {
      if (this.form.title == '') {
        this.form.title = "必填项不能为空"
      }
      else if (this.form.plan == '') {
        this.form.plan = "必填项不能为空"
      }
      else {
        helpCreate(this.form).then(res => {
          window.location.reload()
        })
      }
    },
  }
}
</script>

<style>
.el-drawer__body {
  /* 使抽屉超出后滚动 */
  overflow: scroll;
}
</style>

<style lang="css" scoped>
#add {
  position: relative;
  margin: 0.3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  /* padding: 1rem 0.5rem; */
  color: white;
  border-radius: 5px;
  background: #eeeef1;
  box-shadow: 0 2px 4px #00000033;
}

#time {
  margin-left: 0.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.2rem;
}

#time > div {
  height: 1.2rem;
  margin-right: 1rem;
}

#time img {
  height: 100%;
}

#add_i {
  height: 4rem;
  width: 5.5rem;
  background-color: white;
  z-index: 1;
}

#add_i::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 3.3rem;
  width: 2.5rem;
  margin-left: -1.25rem;
  margin-top: -2.5px;
  border-top: 5px solid #b9b9b9;
}

#add_i::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 3.3rem;
  height: 2.5rem;
  margin-left: -2.5px;
  margin-top: -1.25rem;
  border-left: 5px solid #b9b9b9;
}

.number {
  margin-bottom: -10px;
}

.main header {
  padding: 1.5rem 0.5rem 0.5rem 0.5rem;
  font-size: 1.5rem;
  line-height: 1.5rem;
}

.main header img {
  height: 1.5rem;
}

.item-list {
  padding: 0 0.5rem;
  flex-direction: column;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  /* max-height: 50vh; */
  /* overflow: scroll; */
  margin-bottom: 10rem;
}

.item-list .item {
  position: relative;
  margin: 0.3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  padding: 0 0.5rem;
  color: white;
  border-radius: 5px;
  background: linear-gradient(45deg, #2ec6d8, #2599d2);
  box-shadow: 0 2px 4px #00000033;
}

.item-list :first-child::before {
  background-image: url("assets/clock.png") !important;
  background-position-x: -3px !important;
}

.item-list .item::before {
  content: attr(data-date);
  position: absolute;
  font-size: .5rem;
  width: 30%;
  box-sizing: border-box;
  left: 0;
  top: 10%;
  transform: translateX(-100%);
  color: darkblue;
  /* border-left: 2px solid #26CBCD; */
  height: 110%;
  text-align: right;
  padding-right: 1rem;
  line-height: 5rem;
  background-image: url("assets/line.png");
  background-size: auto 100%;
  background-position-x: 2px;
  background-repeat: no-repeat;
  color: #a2b8bb;
  font-weight: lighter;
}
.title {
  /* margin-top: 0.5rem; */
}
.item-list .item .img-box {
  /* height: 100%; */
  height: auto;
  width: 35%;
  overflow: hidden;
  border-radius: 4px;
  margin: 5px 0;
}

.item-list .item .img-box img {
  /* height: 6rem; */
  width: 100%;
}

.item-list .item .content {
  flex-grow: 1;
  max-width: 60%;
  flex-direction: column;
      justify-content: space-between;
}

.item-list .item .content .title {
  max-width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  /* word-wrap: break-word; */
  white-space: nowrap;
}

.item-list .item .content .id {
  font-size: 5px;
  margin-top: 5px;
  line-height: 2rem;
}

.item-list .item .content .date {
  font-size: 5px;
  margin-top: 1.5rem;
  /* transform: scale(0.8); */
  text-overflow: ellipsis;
  overflow: hidden;
  /* word-wrap: break-word; */
  white-space: nowrap;
}

.item-list .item .content-bottom {
  margin-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.item-list .item button {
  background-color: transparent;
  border-radius: 5px;
  color: white;
  border: 1px solid white;
  height: 1.5rem;
  /* padding: 0.5rem; */
}
</style>


