import request from '@/utils/axios'

// post axios 请求
export function register(data) {
    return request({
        url: '/sign/',
        method: 'post',
        data: {
            username: data.username,
            password: data.password,
            role_object: data.role_object
        }
    })
}


export function messput(data) {
    return request({
        url: '/accounts/',
        method: 'put',
        data
    })
}

// get axios 请求
export function login(data) {
    return request({
        url: '/sign/',
        method: 'get',
        params: {
            username: data.username,
            password: data.password
        }
    })
}

export function admindiv_f() {
    return request({
        url: '/admindiv/',
        method: 'get',
    })
}
// 获取账号详细信息
export function accounts() {
    return request({
        url: '/accounts/',
        method: 'get',
    })
}

// 退出登录，传入token
export function logout() {
    return request({
        url: '/sign',
        method: 'delete',
    })
}


