<template>
  <div class="content">
    <div class="orga">
      <div>
        <img src="../../assets/org/logo.png" />
        </div>
      <div class="mess">
        <p style="font-size: 1.1rem; margin-bottom: 3px">
          {{ info.name }}
          <span v-if="isManager" @click="isDelete()">
            <mt-badge type="error">解散组织</mt-badge>
          </span>
          <span v-else @click="isResign()">
            <mt-badge type="error">退出组织</mt-badge>
          </span>
        </p>
        <p style="font-size: 1rem">地区：{{ info.adminDiv.name }}</p>
        <p style="text-align: left">
          {{ info.inviteCode }}
        </p>
      </div>
      <div class="img" @click="() => $router.push('org/detail')">
        <div></div>
        <img src="../../assets/org/箭头小.png" />
      </div>
    </div>
    <div class="count">
      <div>
        <p>10000</p>
        <p>帮助人数</p>
      </div>
      <div>
        <p>7664</p>
        <p>组织人数</p>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast, MessageBox } from 'mint-ui';
import { orgInfo, dismiss, resign } from '@/api/org'
export default {
  name: 'organizationMsg',
  data () {
    return {
      info: {
        name: '***',
        adminDiv: '*',
        inviteCode: '...'
      }
    }
  },
  computed: {
    isManager () {

      if (JSON.parse(localStorage.getItem('info')).tip === 'manager') {
        console.log(JSON.parse(localStorage.getItem('info')).tip === 'manager')
        return true;
      }
      return false;
    }
  },
  mounted () {
    orgInfo(this.$router.history.current.query.id).then(res => {
      console.log("组织信息", res)
      this.info = res
    }).catch(err => {
      if (err) {
        Toast('暂无组织信息');
      }
    })
  },
  methods: {
    // 管理员删除组织
    isDelete () {
      MessageBox.confirm('是否确认解散该组织？').then(action => {
        dismiss().then(res => {
          this.$router.push('/personal')
        })
      });
    },
    // 志愿者退出组织
    isResign () {
      MessageBox.confirm('是否确认退出该组织？').then(action => {
        resign().then(res => {
          this.$router.push('/personal')
        })
      });
    }
  }
}
</script>

<style scoped>
.content {
  margin: 0;
  padding: 0;
  max-width: 100vw;
  background-color: white;
  margin-bottom: 0.3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.content .count {
  display: flex;
  flex-direction: row;
}

.orga {
  max-width: 100%;
  height: 4rem;
  padding: 0.5rem;
  align-items: center;
  border-bottom: 0.2px solid #f0f6f7;
}

.orga {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.orga > div:first-child {
  height: 3.5rem;
  padding-right: 0.3rem;
}

.orga img {
  height: 100%;
}
.mess {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-grow: 1;
  margin-left: 1rem;
  box-sizing:content-box;
}

.mess > p,
.count p {
  margin: 0;
  padding: 0;
}

.mess > p:last-child {
  font-weight: lighter;
  font-size: 0.8rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.img {
  height: 1rem;
  margin-right: 0;
}

.img img {
  height: 100%;
}

.count > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  font-size: 0.8rem;
  padding: 0.5rem 0;
}

.count > div:first-child {
  border-right: 0.2px solid #f0f6f7;
}

footer {
  width: 100%;
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-to {
  transform: translateX(-10px);
  opacity: 0;
}
.slide-fade-enter {
  transform: translateX(10px);
  opacity: 0;
}
</style>


