import Vue from 'vue'
import Router from 'vue-router'
import personal from '@/pages/personal/personal'
import personalInfo from '@/pages/personal/personalInfo'
import headWithBack from '@/pages/headWithBack'
import signIn from '@/pages/sign/signIn'
import signUp from '@/pages/sign/signUp'
import organization from '@/pages/organization/organization'
import init from '@/pages/init/init'
import orgDetail from '@/pages/organization/orgDetail'
import home from '@/pages/home/home'
import articleDetail from '@/components/articleDetail'
import vltDetail from '@/components/vltDetail'
import txyarticleDetail from '@/components/txyarticleDetail'
import message from '@/pages/message/message'
import agreement from '@/components/agreement'
import notice from '@/pages/notice/notice'
import helpCenter from '@/pages/helpCenter/helpCenter'
import helpEvent from '@/pages/helpEvent/helpEvent'
import event from '@/pages/helpEvent/event'
import volunteer from '@/pages/volunteer/volunteer'
import advice from '@/components/personal/advice'

Vue.use(Router)

export default new Router({
    mode: "hash",
    routes: [{
            path: '/',
            redirect: '/home',
        },
        {
            path: '/detail',
            name: 'detail',
            component: articleDetail
        },
        {
            path: '/vltdetail',
            name: 'vltdetail',
            component: vltDetail
        },
        {
            path: '/txydetail',
            name: 'txydetail',
            component: txyarticleDetail
        },
        {
            path: '/notice',
            name: 'notice',
            component: notice
        },
        {
            path: '/event',
            name: 'event',
            component: event
        },
        {
            path: '/volunteer',
            name: 'volunteer',
            component: volunteer
        },
        {
            path: '/helpCenter',
            name: 'helpCenter',
            component: helpCenter
        },
        {
            path: '/helpEvent',
            name: 'helpEvent',
            component: helpEvent
        },
        {
            path: '/message',
            name: 'message',
            component: message
        },
        {
            path: '/agreement',
            name: 'agreement',
            component: agreement
        },
        {
            path: '/headWithBack',
            name: 'headWithBack',
            component: headWithBack
        },
        {
            path: '/signin',
            name: 'signin',
            component: signIn
        },
        {
            path: '/signup',
            name: 'signup',
            component: signUp
        },
        {
            path: '/init',
            name: 'init',
            component: init
        },
        {
            path: '/home',
            name: 'home',
            component: home
        },
        {
            path: '/personal',
            name: 'personal',
            component: personal,
        },
        {
            path: '/personal/org',
            name: 'personal/org',
            component: organization,
        },
        {
            path: '/personal/org/detail',
            name: 'personal/org/detail',
            component: orgDetail,
        },
        {
            path: '/personal/info',
            name: '/personal/info',
            component: personalInfo
        },
        {
            path: '/personal/advice',
            name: '/personal/advice',
            component: advice
        },
    ]
})


