<template>
  <div id="box">
    <div id="nav" :style="{ backgroundColor: bcolor }">
      <ul>
        <li @click="back()" :style="{ fontWeight: fw }">
          推荐
          <div
            v-show="vshow == true"
            style="
              border-top: 3px solid black;
              width: 50%;
              margin-left: 25%;
              margin-top: 0.2rem;
            "
          ></div>
        </li>
        <li @click="change()" :style="{ fontWeight: txyfw }">
          童心圆
          <div
            v-show="vshow == false"
            style="
              border-top: 3px solid black;
              width: 50%;
              margin-left: 25%;
              margin-top: 0.2rem;
            "
          ></div>
        </li>
      </ul>
      <div>
        <img :src="img" />
        <!-- <img src="../../assets/home/背景.png" /> -->
      </div>
    </div>
    <homeArticle v-show="vshow == true"></homeArticle>
    <txyArticle v-show="vshow == false"></txyArticle>
    <footer_
      :logo="logoimg"
      :lcolor="logocolor"
      :plogo="plogoimg"
      :plcolor="plogocolor"
    ></footer_>
  </div>
</template>
<script>

import homeArticle from '@/pages/home/homeArticle'
import txyArticle from '@/pages/home/txyArticle'
import footer_ from '@/pages/footer'

export default {
  name: 'home',
  components: {
    homeArticle, footer_, txyArticle
  },
  data () {
    return {
      vshow: true,
      img: require('../../assets/home/背景.png'),
      txyfw: 'normal',
      fw: 'bold',
      bcolor: ' #fb4b4b',
      logoimg: require('../../assets/bar/首页-select.png'),
      logocolor: '#26cbcd',
      plogoimg: require('../../assets/bar/我的.png'),
      plogocolor: '333',

    }
  },
  methods: {
    change () {
      this.vshow = false
      this.img = require('../../assets/home/txy背景.png')
      this.txyfw = 'bold'
      this.fw = 'normal'
      this.bcolor = '#EBC08D'
    },
    back () {
      this.vshow = true
      this.img = require('../../assets/home/背景.png')
      this.fw = 'bold'
      this.txyfw = 'normal'
      this.bcolor = ' #fb4b4b'
    },
  }
}
</script>

<style scoped>
#box {
  margin: 0;
  color: #353535;
  overflow: scroll;
}
li {
  list-style: none;
}
#nav {
  display: flex;
  flex-direction: column;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 3.5rem;
}
ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  font-size: 1.2rem;
}
ul li:first-child {
  margin-right: 3rem;
}
#nav > div {
  width: 90%;
  margin-bottom: -2.5rem;
  margin-left: 5%;
  height: 6rem;
}
#nav img {
  width: 100%;
  height: 100%;
}
</style>



