<template>
  <div id="box">
    <p>成员信息</p>
    <div v-if="memberList.length > 0">
      <div class="content" v-for="item in memberList" v-bind:key="item.id">
        <div class="head">
          <img src="../../assets/org/1.png" />
          <!-- 这里性别我给私自取消了 -->
          <!-- <div class="sex">
            <img src="../../assets/org/woman.png" />
          </div> -->
        </div>
        <div id="main">
          <div class="message">
            <p>{{ item.role_object.name }}</p>
            <p style="color: #AAA">[{{ item.role_object.tip }}]</p>
            <p>({{ item.username }})</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { orgMemberList, orgInfo } from '@/api/org'
export default {
  name: 'organizationMem',
  data () {
    return {
      memberList: [],
    }
  },
  mounted () {
    orgMemberList(this.$router.history.current.query.id).then(res => {
      // console.log("获取成员列表成功:", res)
      this.memberList = res
    })
  }
}
</script>

<style scoped>
#box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100vw;
  height: 100vh;
  background-color: white;
}

#box > p {
  font-size: 1.1rem;
  font-weight: bold;
  padding: 1rem 1rem;
}

.content {
  width: 100vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom: 1px solid #f0f6f7;
  padding: 0.5rem 1rem;
  /* width: 100%; */
  background-color: white;
}

.head {
  height: 3.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 1rem;
}

.head > img {
  height: 100%;
}

.sex {
  height: 1.3rem;
  margin: 2.6rem 0 0 -1rem;
}

.sex > img {
  height: 100%;
}

.message {
  color: black;
  font-size: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

#main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.message > p:last-child {
  color: #2599d2;
  font-size: 0.8rem;
  margin-left: 0.3rem;
}
</style>



