<template>
  <div id="box">
    <div id="form">
      <div class="write">
        <label>真实姓名：</label>
        <input
          type="text"
          :readOnly="isReadonly"
          v-model="form.name"
          class="message-input yujian-input"
          placeholder="请输入真实姓名"
        />
      </div>
      <div class="write">
        <span>身份证号：</span>
        <input
          type="text"
          :readOnly="isReadonly"
          v-model="form.cardNum"
          class="message-input yujian-input"
          placeholder="身份证号"
        />
      </div>
      <div class="write" v-if="form.tip == 'volunteer'">
        <span>学院：</span>
        <input
          type="text"
          :readOnly="isReadonly"
          v-model="form.college"
          class="message-input yujian-input"
          placeholder="请填写学院名称"
        />
      </div>
      <div class="write" v-if="form.tip == 'volunteer'">
        <span>学号：</span>
        <input
          type="text"
          :readOnly="isReadonly"
          v-model="form.schoolId"
          class="message-input yujian-input"
          placeholder="请输入学号"
        />
      </div>
      <div class="write" v-if="form.tip == 'child'">
        <span>学校：</span>
        <input
          type="text"
          :readOnly="isReadonly"
          v-model="form.school"
          class="message-input yujian-input"
          placeholder="请输入学校名称"
        />
      </div>
      <div class="write" v-if="form.tip == 'volunteer'">
        <span>专业：</span>
        <input
          type="text"
          :readOnly="isReadonly"
          v-model="form.major"
          class="message-input yujian-input"
          placeholder="请输入专业名称"
        />
      </div>
      <div class="write">
        <span>籍贯：</span>
        <select
          v-model="option_value"
          class="message-input yujian-input"
          id="adDiv"
          :disabled="showSelect"
        >
          <option v-if="returnlist" value="0">暂无地址</option>
          <option v-else value="0">请选择地址</option>
          <option
            v-for="(item, index) of listArray"
            :value="item.id"
            :key="index"
          >
            {{ item.name }}
          </option>
        </select>
      </div>
      <div class="write">
        <span>手机号：</span>
        <input
          :readOnly="isReadonly"
          type="text"
          id="phone"
          v-model="form.username"
          placeholder="手机号码"
          class="message-input yujian-input"
        />
      </div>
      <input
        type="submit"
        class="yujian-input"
        :value="butt"
        @click="butt == '结队' ? relate() : edit()"
      />
    </div>
    <slot name="form"></slot>
  </div>
</template>

<script>
import { admindiv_f, messput, accounts } from '@/api/account'
import { volunteer, makeTeam } from '@/api/growup'
import { Toast, MessageBox } from 'mint-ui';
import bus from '../main'

export default {
  name: 'messForm',
  props: ['relate_butt', 'returnlist'],
  data () {
    return {
      showSelect: true,
      isReadonly: true,
      flag: true,
      listArray: [],
      list: [],
      option_value: 0,
      butt: this.relate_butt || '编辑',
      isPair: false,
      id: '',
      form: {
        name: '',
        cardNum: '',
        college: '',
        schoolId: '',
        option_value: '0',
        username: '',
        school: '',
        major: '',
        tip: '',
      }
    }
  },
  mounted () {
    admindiv_f().then(res => {
      this.listArray = res
    })
    if (this.relate_butt == '编辑') {
      accounts().then(res => {
        this.form = res.role_object
        this.form['username'] = res.username
        this.option_value = res.adminDiv || 0
        bus.$emit('kerwin', this.form)
      })
    } else if (this.relate_butt == '结队') {
      volunteer().then(res => {
        this.list = res
        for (let index = 0; index <= this.list.length - 1; index++) {
          if (this.list[index].id == this.$router.history.current.query.Id) {
            this.form = this.list[index].role_object
            this.form['username'] = this.list[index].username
            this.option_value = this.form.adminDiv || 0
            bus.$emit('kerwin', this.form)

          }
        }
      })
    }
  },
  methods: {
    returnInfo (i) {
      let info = { username: i.username, ...i.role_object }
      return info
    },
    // 结队
    relate () {
      if (this.butt == '结队') {
        accounts().then(res => {
          this.id = res.id
          // console.log("id" + this.id)
          makeTeam(
            {
              'child_id': this.id,
              'volunteer_id': this.$router.history.current.query.Id
            }
          ).then(res => {
            Toast("结队成功")
            clearTimeout(this.timer);  //清除延迟执行 
            this.timer = setTimeout(() => {   //设置延迟执行
              this.$router.push('/volunteer');
            }, 3000);
            // let timer = setTimeout(this.$router.push('/volunteer'), 5000)
          })
        })
      }
    },
    // 编辑信息
    edit () {
      console.log(this.butt == '编辑')
      if (this.butt == '完成') {
        this.showSelect = true
        this.isReadonly = true
        let that = this
        this.form.adminDiv = this.option_value
        messput(
          this.form
        ).then(res => {
          console.log('信息修改' + res)
          that.butt = '编辑'
        }).catch(error => {
          alert('修改信息失败')
          that.butt = '编辑'
        })
      }
      else {
        this.butt = '完成'
        this.showSelect = false
        this.isReadonly = false
        return
      }
    },
  }
}
</script>

<style lang="css" scoped>
#b {
  background-color: white;
  z-index: 0;
}

#form {
  margin-top: 3rem;
  margin-bottom: 3rem;
  width: 80%;
  margin-left: 10%;
  display: flex;
  flex-direction: column;
  background-color: white;
}

.yujian-input {
  box-sizing: border-box;
  border: none;
  border-radius: 25px;
  margin: 0.7rem 0;
  padding: 10px 20px;
  font-size: 1rem;
  margin-right: 0px;
  width: 70%;
}

.yujian-input[type="submit"] {
  width: 100%;
  background-color: #2599d2;
  color: white;
  font-size: 1.2rem;
  line-height: 1.2rem;
  margin-bottom: 3rem;
  margin-top: 3rem;
}

.write_p > input {
  box-sizing: border-box;
  border: none;
  background-color: white;
  border-radius: 25px;
  color: #aaa;
  margin: 0.7rem 0;
  padding: 10px 20px;
  font-size: 1rem;
  width: 70%;
}

#form > div,
.write_n,
.write_p {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.write_n {
  margin: 0.8rem 0;
  margin-right: 40%;
}

.write > select {
  width: 70%;
}
</style>


