<template>
  <div>
    <headWithBack title="项目日志" :showTitle="true"></headWithBack>
    <section>
      <div class="block">
        <div class="radio">
          排序：
          <el-radio-group v-model="reverse">
            <el-radio :label="true">倒序</el-radio>
            <el-radio :label="false">正序</el-radio>
          </el-radio-group>
        </div>

        <el-timeline>
          <el-timeline-item
            v-for="(item, index) in list"
            :key="index"
            type="primary"
            size="large"
            :timestamp="item.user + item.time"
            placement="top"
          >
            <el-card>
              <h4>{{ item.title }}</h4>
              <div v-for="o in item.contents" :key="o" class="text item">
                {{ o }}
              </div>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </div>
    </section>
  </div>
</template>

<script>
import headWithBack from '@/pages/headWithBack'

export default {
  name: 'advice',
  components: { headWithBack },
  data () {
    return {
      reverse: true,
      list: [{
        title: '😝遇见v1.0上线',
        contents: [
          '⚪接口基本对接完毕',
          '⚪独立完成部分基本都写了注释',
          '⚪初步测试基本通过能够完成正常逻辑，部分问题等待后续版本更新',
          '🔺剩余接口',
          '⭐ 结束结对（在页面中找不到合适的位置放置按钮）',
          '⭐ 删除结对（同上）',
          '⭐ 修改组织信息（同上）',
          '⭐ 修改个人信息（部分字段因权限无法修改，需要根据角色判断要发送的字段，因事件紧迫故暂时搁置）',
        ],
        time: '2021/5/7',
        size: 'large',
        user: '崔雨'
      }]
    };
  }
}
</script>

<style scoped>
* {
  text-align: left;
}
.radio {
  margin-bottom: 3rem;
}
h4 {
  font-weight: bold;
  margin: 0.5rem;
  font-size: 1.2rem;
}
.block {
  width: 95vw;
  margin: 0 auto;
  font: 1rem;
}
.item {
  margin: 0.3rem;
}
</style>


