<template>
  <footer>
    <!-- todo  针对不同页面显示不同的图标颜色 参考文档：组件传值-->
    <div class="toolBar" style="padding: 0">
      <div class="toolBar-box togg" @click="() => $router.push('/home')">
        <img class="toolBar-icon" :src="logo" alt="" srcset="" />
        <p class="toolBar-text" :style="{ color: lcolor }">首页</p>
      </div>
      <div class="toolBar-box" @click="() => $router.push('/volunteer')">
        <img
          class="toolBar-help toolBar-icon"
          src="../assets/bar/求助.png"
          alt=""
          srcset=""
        />
      </div>
      <div class="toolBar-box toggle" @click="() => $router.push('/personal')">
        <img class="toolBar-icon" :src="plogo" alt="" srcset="" />
        <p class="toolBar-text" :style="{ color: plcolor }">我的</p>
      </div>
    </div>
  </footer>
</template>

<script>
import { accounts } from '@/api/account'

export default {
  name: 'footer_',
  data () {
    return {

    }
  },
  mounted () {
  },
  props: ['logo', 'lcolor', 'plogo', 'plcolor',]
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.toolBar {
  background-color: #ffffff;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  overflow: visible;
  position: fixed;
  bottom: 0px;
  right: 0px;
  height: 4rem;
  width: 100%;
  z-index: 99;
}

.toolBar-icon {
  height: 1.4rem;
  width: 1.4rem;
}

.toolBar-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 80%;
}

.toolBar-text {
  color: #646464;
  font-size: 0.8rem;
}

.toolBar-help {
  transform: scale(4) translateY(-7px);
  border: 1.5px solid white;
  border-radius: 50%;
  box-sizing: border-box;
  overflow: hidden;
  background: white;
  overflow: visible;
}
</style>



