<template>
  <div id="box">
    <!-- <headWithBack title="帮扶大事记" :showTitle="true"></headWithBack> -->
    <div class="event-chunk">
      <div class="chunk yujian-color-orange">
        <p class="number">{{ data.plans_3 }}</p>
        <p>学业帮扶</p>
      </div>
      <div class="chunk yujian-color-blue">
        <p class="number">{{ data.plans_2 }}</p>
        <p>心理帮扶</p>
      </div>
      <div class="chunk yujian-color-green">
        <p class="number">{{ data.plans_1 }}</p>
        <p>生涯帮扶</p>
      </div>
    </div>
    <!-- 时间线组件 -->
    <timeline></timeline>
    <footer>
      <p>已经到底了哦~</p>
    </footer>
    <footer_
      :logo="logoimg"
      :lcolor="logocolor"
      :plogo="plogoimg"
      :plcolor="plogocolor"
    ></footer_>
  </div>
</template>

<script>
import headWithBack from '@/pages/headWithBack'
import timeline from '@/components/timeline'
import footer_ from '@/pages/footer'
import { helpnumber } from '@/api/help'

export default {
  name: 'helpEvent',
  components: { headWithBack, timeline, footer_ },
  data: function () {
    return {
      logoimg: require('../../assets/bar/首页.png'),
      logocolor: '333',
      plogoimg: require('../../assets/bar/我的.png'),
      plogocolor: '333',
      id: JSON.parse(localStorage.getItem('info')).pair_id,
      // 数据：{学业， 心理， 生涯} 
      data: {
        plans_1: 0,
        plans_2: 0,
        plans_3: 0,
      }
    }
  },
  mounted () {
    // 获取生涯详情数据
    helpnumber(this.id).then(res => {
      // console.log(res)
      this.data = res
    })
  }
}
</script>

<style lang="css" scoped>
.event-title {
  font-size: 1.5rem;
  text-align: center;
  margin-top: 1rem;
}

.event-chunk {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 0.5rem;
  color: white;
  font-size: 0.8rem;
}

.event-chunk .chunk {
  width: 32%;
  text-align: center;
  border-radius: 8px;
  box-shadow: 1px 2px 1rem #ad96969d;
  padding: 0.5rem 0;
}

.event-chunk .chunk .number {
  font-weight: bold;
  font-size: 1.5rem;
}

.number {
  margin-bottom: 10px;
}

.yujian-color-orange {
  background: linear-gradient(30deg, #f24645, #ebc08d);
  position: relative;
  overflow: hidden;
}

.yujian-color-green::after,
.yujian-color-blue::after,
.yujian-color-orange::after {
  position: absolute;
  top: 2rem;
  left: -4rem;
  content: "";
  background: linear-gradient(40deg, #e5e5e518, #ffffff10);
  height: 12rem;
  width: 12rem;
  border-radius: 50%;
  z-index: 2;
}

.yujian-color-green::before,
.yujian-color-blue::before,
.yujian-color-orange::before {
  position: absolute;
  top: -3rem;
  right: -7rem;
  content: "";
  background: linear-gradient(40deg, #c2c2c211, #ffffff10);
  height: 12rem;
  width: 12rem;
  border-radius: 50%;
  z-index: 1;
}

.yujian-color-blue {
  position: relative;
  overflow: hidden;
  background: linear-gradient(30deg, #4f6dee, #67bdf9);
}

.yujian-color-green {
  position: relative;
  overflow: hidden;
  background: linear-gradient(30deg, #26cbcd, #2599d2);
}
footer > p {
  margin-bottom: 8rem;
}
</style>


