<template>
  <div id="head_c">
    <div id="head_s">
      <div id="head_message">
        <div id="head_img">
          <img src="../../assets/personal_header/头像.png" />
          <div id="sex">
            <img :src="seximg" />
          </div>
        </div>
        <div id="head_me">
          <p id="name">{{ name }}</p>
          <p id="school">{{ organization || "暂无组织" }}</p>
        </div>
      </div>
      <div>
        <label>
          <p @click="jump()">个人信息</p>
          <div class="arrows">
            <img src="../../assets/personal_header/箭头.png" />
          </div>
        </label>
      </div>
    </div>
  </div>
</template>

<script>
import { accounts } from '@/api/account'
import bus from '@/main'

export default {
  name: 'personalHeader',
  data () {
    return {
      name: '',
      sex: '',
      seximg: '',
      schoolId: '',
      cardNum: '',
      organization: '小学'
    }
  },
  mounted () {
    if (!localStorage.getItem('token')) {
      // if (alert('未登录，请前往登录')) {
      this.$router.push('signin')
    } else {
      accounts().then(res => {
        this.name = res.role_object.name
        this.organization = res.organization
        bus.$emit('org_id', this.organization) // 发出org_id信号，传递组织id，由personalNav接收并渲染
        localStorage.setItem('info', JSON.stringify(res.role_object))
        this.cardNum = res.role_object.cardNum
        // localStorage.setItem('')
        // window.location.reload()
      })
    }
    this.changeImg()
  },
  methods: {
    jump () {
      this.$router.push('/message')
    },
    changeImg () {
      this.change()
      if (this.sex == '0') {
        this.seximg = require('@/components/assets/性别女.jpg')
      } else {
        this.seximg = require('@/components/assets/性别男.png')
      }
    },
    change () {
      let sexif = this.cardNum.substr(-2, 1)
      let sexn = parseInt(sexif)
      if (sexn % 2 == 0) {
        this.sex = '0'
      } else if (sexn % 2 == 1) {
        this.sex = '1'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: #fff;
}

#head_c,
#head_m,
#head_v {
  color: #fff;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-size: cover;
  justify-content: space-around;
  background-repeat: no-repeat;
}

#head_v {
  background-image: url("../../assets/personal_header/背景绿.png");
}

#head_m {
  background-image: url("../../assets/personal_header/背景蓝.png");
}

#head_c {
  background-image: url("../../assets/personal_header/背景黄.png");
}

#head_s {
  width: 95%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 2rem;
  margin-bottom: 2rem;
  align-items: center;
}

#head_message {
  display: flex;
  flex-direction: row;
  align-items: center;
}

#head_img {
  display: flex;
  flex-direction: row;
}

#head_img > img {
  height: 4.5rem;
  border-radius: 50%;
}

#sex > img {
  height: 1rem;
  border-radius: 50%;
  margin: 3rem 0 0 -1.2rem;
}

#head_me {
  margin-left: 0.5rem;
  text-align: left;
}

#head_me > p:first-child {
  font-size: 1.1rem;
  margin-bottom: 0.8rem;
}

.arrows img {
  height: 0.8rem;
  vertical-align: bottom;
}

#school {
  opacity: 0.8;
  font-size: 0.5rem;
}

.arrows {
  line-height: 16px;
  color: #999999;
}

label {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 0.8rem;
}

label img {
  padding: 0 0.5rem 0 0.9rem;
}

#head_me > p:first-child {
  font-size: 1.1rem;
  margin-bottom: 0.8rem;
}
</style>



