import request from '@/utils/axios'

// post axios 请求
export function article (start = 0, categ = 1) {
  return request({
    url: `/articles/?start=${start}&categ=${categ}`,
    baseURL: 'http://47.104.89.108:8075',
    method: 'get',
  })
}

export function articleDetail (id) {
  return request({
    url: `/articles/${id}`,
    baseURL: 'http://47.104.89.108:8075',
    method: 'get',
  })
}

export function txyarticle (start = 0, categ = 2) {
  return request({
    url: `/articles/?start=${start}&categ=${categ}`,
    baseURL: 'http://47.104.89.108:8075',
    method: 'get',
  })
}

export function txyarticleDetail (id) {
  return request({
    url: `/articles/${id}`,
    baseURL: 'http://47.104.89.108:8075',
    method: 'get',
  })
}


